<template>
  <div class="product d-flex flex-column h-100">
    <Top />
    <main role="main" class="flex-shrink-0 mb-4">
      <b-container>
        <b-row class="text-center">
          <b-col cols="3" class="d-none d-md-block">
            <SideCategory />
          </b-col>
          <b-col
            v-if="currProduct !== null"
          >
            <b-breadcrumb :items="breadcrumb"></b-breadcrumb>
            <b-row class="text-left mb-5">
              <b-col md="6" sm="12">
                <b-media-aside class="flex-column mr-2" style="max-width:400px;">
                  <b-img
                    :src="require(`../assets/${currProduct.id}_R.jpg`)"
                    :alt="currProduct.name" class="w-100"></b-img>

                  <div class="d-flex align-items-center py-4">
                    <b-button size="sm">
                      <font-awesome-icon icon="caret-left" />
                    </b-button>
                    <div id="thumbnail-wrapper" class="d-inline-block text-nowrap px-1">
                      <ul class="list-inline position-relative m-0">
                        <li class="list-inline-item"
                          v-for="(thumbnail, index) in currProduct.thumbnails"
                          :key="index"
                        >
                          <b-img thumbnail
                            :src="require(`../assets/${thumbnail}.jpg`)"
                            :alt="currProduct.name" />
                        </li>
                      </ul>
                    </div>
                    <b-button size="sm">
                      <font-awesome-icon icon="caret-right" />
                    </b-button>
                  </div>

                  <h6>分享到</h6>
                  <ul class="list-inline">
                      <li class="list-inline-item">
                        <font-awesome-icon :icon="['fab', 'line']" size="2x" :style="{ color: '#00c300' }" />
                      </li>
                      <li class="list-inline-item">
                        <font-awesome-icon :icon="['fab', 'facebook']" size="2x" :style="{ color: '#3b5998' }" />
                      </li>
                      <li class="list-inline-item">
                        <font-awesome-icon :icon="['fab', 'whatsapp']" size="2x" :style="{ color: '#25d366' }" />
                      </li>
                    </ul>
                </b-media-aside>
              </b-col>
              <b-col>
                <b-media-body class="ml-3">
                  <h3 class="mt-0 mb-3">{{ currProduct.name }}</h3>
                  <p class="short-desc border-left pl-1 mb-3">
                    <span class="d-block"
                      v-for="(shortDesc, index) in currProduct.shortDescriptions"
                      :key="index"
                    >{{ shortDesc }}</span>
                  </p>
                  <p class="price">{{ getPrice() }}</p>
                  <b-form>
                    <b-form-group
                      label="數量"
                      label-for="input-small"
                    >
                      <div class="row m-0">
                        <div class="col-6 p-0">
                          <b-button-group size="sm">
                            <b-button variant="outline-secondary"><font-awesome-icon icon="minus" /></b-button>
                            <b-form-input id="input-small" size="sm" placeholder="數量" value="1"></b-form-input>
                            <b-button variant="outline-secondary"><font-awesome-icon icon="plus" /></b-button>
                          </b-button-group>
                        </div>
                        <div class="col-6 p-0">
                          <b-button
                          size="sm"
                          variant="dark"
                          class="ml-1">加入購物車</b-button>
                        </div>
                      </div>
                    </b-form-group>
                  </b-form>
                  <b-button variant="outline-secondary" size="sm">
                    <font-awesome-icon :icon="['far', 'heart']" />
                    加入追蹤清單
                  </b-button>
                  <h6 class="mt-3">付款方式</h6>
                  <div class="pl-4">
                    <span
                      v-for="(payment, index) in currProduct.payment"
                      :key="index" :class="index !== 0 ? 'pl-2 payment' : ''"
                    >{{ payment }}</span>
                  </div>
                  <h6 class="mt-3">送貨方式</h6>
                  <ul>
                      <li
                        v-for="(delivery, index) in currProduct.delivery"
                        :key="index"
                      >{{ delivery }}</li>
                    </ul>
                </b-media-body>
              </b-col>
            </b-row>
            <div class="description text-left">
              {{ currProduct.description }}
            </div>
            <div class="details text-left">
              <img src="../assets/A0001.jpg" />
            </div>
          </b-col>
        </b-row>
      </b-container>
    </main>
    <StickyFooter />
  </div>
</template>

<script>
import { mapState } from 'vuex'

import Top from '@/components/Top.vue'
import StickyFooter from '@/components/StickyFooter.vue'
import SideCategory from '@/components/SideCategory.vue'

import { library } from '@fortawesome/fontawesome-svg-core'
import { faMinus, faPlus, faCaretLeft, faCaretRight } from '@fortawesome/free-solid-svg-icons'
import { faHeart } from '@fortawesome/free-regular-svg-icons'
import { faLine, faFacebook, faWhatsapp } from '@fortawesome/free-brands-svg-icons'

library.add(faMinus, faPlus, faCaretLeft, faCaretRight,
  faHeart, faLine, faFacebook, faWhatsapp)

export default {
  name: 'Product',
  components: {
    Top,
    StickyFooter,
    SideCategory
  },
  data () {
    return {
      breadcrumb: [
        {
          text: 'TODO::',
          href: '#'
        },
        {
          text: 'Category',
          href: '#'
        },
        {
          text: 'Filter',
          active: true
        }
      ],
      currProduct: null
    }
  },
  computed: {
    ...mapState([
      'products'
    ])
  },
  mounted () {
    for (let index = 0; index < this.products.length; index++) {
      if (this.products[index].id === this.$route.params.id) {
        this.currProduct = this.products[index]
        break
      }
    }
  },
  methods: {
    getProduct () {
      let product

      for (let index = 0; index < this.products.length; index++) {
        if (this.products[index].id === this.$route.params.id) {
          product = this.products[index]
          break
        }
      }
      return product
    },
    getPrice () {
      return 'NT$' + this.currProduct.price
    }
  }
}
</script>

